        css() {
            return `
                .ecom-product-single-label{
                    display:flex;
                    flex-direction: row;
                    gap:5px;
                }
                .ecom-product-single__properties-list{
                    width: 100%;
                    position:relative;
                    display: flex;
                    flex-wrap: wrap;
                }
                .ecom-product-single__properties-list-item{
                    display: flex;
                    flex-direction: column;
                }
                .ecom-product-single-label ~ label {
                    display: flex;
                    align-items: center;
                }
                .ecom-product-single-label ~ label:last-child {
                    margin-bottom: 0px;
                }
                .ecom-product-single-label {
                    display: block;
                    cursor:pointer;
                }

                .ecom-product-single__property-checkbox[type=checkbox]{
                        -webkit-appearance: none;
                        -moz-appearance: none;
                        appearance: none;
                        padding: 0;
                        -webkit-print-color-adjust: exact;
                        color-adjust: exact;
                        display: inline-block;
                        vertical-align: middle;
                        background-origin: border-box;
                        -webkit-user-select: none;
                        -moz-user-select: none;
                        -ms-user-select: none;
                        user-select: none;
                        flex-shrink: 0;
                    }

                    .ecom-product-single__property-checkbox[type=checkbox]:checked {
                        border-color: transparent;
                        background-size: 100% 100%;
                        background-position: center;
                        background-repeat: no-repeat;
                        background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");
                    }
                    .ecom-product-single__property-radio{
                        -webkit-appearance: none;
                        -moz-appearance: none;
                        appearance: none;
                        padding: 0;
                        -webkit-print-color-adjust: exact;
                        color-adjust: exact;
                        display: inline-block;
                        vertical-align: middle;
                        background-origin: border-box;
                        -webkit-user-select: none;
                        -moz-user-select: none;
                        -ms-user-select: none;
                        user-select: none;
                        flex-shrink: 0;
                        position:relative;
                    }

                    .ecom-product-single__property-radio:checked::after{
                        position:absolute;
                        content:'';
                        left:50%;
                        top:50%;
                        transform:translate(-50%,-50%)
                    }

                    @media screen and (max-width: 767px) {
                        .ecom-product-single__properties-list-item{
                            width: 100% !important
                        }
                    }
            `;
