    methods: {
        formWidth(field) {
            return {
                width: field.settings?.width
                    ? `calc(${
                          field.settings.width !== '100%'
                              ? field.settings.width
                              : field.settings.width +
                                ` + ${
                                    this.data.settings.column_gap
                                        ? this.data.settings.column_gap / 2 + 'px'
                                        : '5px'
                                }`
                      } - ${this.data.settings.column_gap ? this.data.settings.column_gap / 2 + 'px' : '5px'})`
                    : '100%'
            };
        }
